*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
/*   border: 1px solid black; */
}

html {
  font-size: 62.5%;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(20,59,69);
  background: linear-gradient(180deg, rgb(28, 28, 28) 0%, rgb(21, 62, 75) 99%, rgb(13, 51, 59) 100%); 
  font-size: 3.5rem;
}