.app-logo {
  width: 7vw;
  pointer-events: none;
  position: fixed;
  top: 1%;
  left: 1%;
}

@media (prefers-reduced-motion: no-preference) {
  .app-logo {
    animation: app-logo-spin infinite 20s linear;
  }
}

@keyframes app-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app {
  width: 100vw;
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

#clock {
  width: 50%;
  height: 30%;
}

#clock-display {
  font-size: 10rem;
  color: rgb(135, 86, 206);
  height: 60%;
}

#timer-label {
  color: rgb(41, 159, 120);
  font-size: 3rem;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#time-left {
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#control-panel {
  color: rgb(135, 86, 206);
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.control-group {
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.controls {
  width: 100%;
  height: 40%;
  display: flex;
  justify-content: space-evenly;
}

#session-length, #break-length {
  height: 40%;
}

#session-label, #break-label {
  color: rgb(41, 159, 120);
  font-size: 2.3rem;
  height: 20%;
  margin-bottom: 0.5rem;
}

button {
  background: none;
  border: none;
  height: 100%;
  aspect-ratio: 1/1;
  z-index: 2;
}

button:hover {
  background-color: rgba(138, 145, 151, 0.168);
  cursor: pointer;
}

.icons {
  font-size: 2.5rem;
  color: rgb(41, 159, 120);
}

@media (max-width: 27.5em) {
  #timer-buttons {
    flex-direction: column;
    height: 80%;
    width: auto;
  }

  #timer-buttons button {
    height: 50%;
  }

}

@media (max-width: 21.25em) {
  #clock {
    width: 70%;
  }
}